main {
  margin: 0 auto;
  max-width: 90%;
  padding: 50px 30px;
}

* {
  box-sizing: border-box;
}

html, body {
  min-height: 100%;
}

body {
  margin: 0;
  padding: 0;
  font-weight: 600;
  line-height: 1.5;
  font-size: 18px;
  letter-spacing: .1em;
  background: #1F3B60;
  color: #fafafa;
  font-family: 'Open Sans', sans-serif;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Aladin', cursive;
  font-weight: 500;
  margin: 5px 0;
  line-height: 1.25;
}

h1 {
  font-size: 5rem;
  margin: 50px 20px;
  text-shadow: 5px 5px 10px rgba(0,0,0,.5);
}

h2 {
  font-size: 2rem;
}

h3 {
  font-size: 1.75rem;
}

h4 {
  font-size: 1.5rem;
}

h5 {
  font-size: 1.25rem;
}

h6 {
  font-size: 1rem;
}

p {
  font-size: 1.1rem;
  margin: 20px 0;
  line-height: 2rem;
}

a {
  color: #fafafa;
  text-decoration: none;
  opacity: .5;
}

a:hover {
  opacity: 1;
  transition: all .2s ease-in-out;
}

header {
  background: #799FBA;
}

header a {
  color: #fafafa;
}

button {
  font-size: 1rem;
  font-weight: 600;
  text-decoration: none;
  border-radius: .5rem;
  border: none;
  color: #fff;
  outline: none;
  cursor: pointer;
  margin: 0 1px;
  background-color: #799FBA;
  padding: 10px 15px;
  opacity: .7;
}

button:hover {
  opacity: 1;
}

button[disabled] {
  cursor: default;
  opacity: .8;
}

img {
  max-width: 100%;
}

span[role='img'] {
  cursor: default;
}

form {
  width: 40%
}

input {
  margin: 10px 0;
  font-size: 1rem;
  padding: .25rem;
  width: 600px;
  border: 1px solid #799FBA;
  outline: none;
  border-radius: 3px;
  transition: box-shadow .2s;
}

input:focus {
  border: 1px solid #31343D;
  box-shadow: 0 0 /25rem rgba(0, 0, 0, .5);
}

label {
  margin-right: 10px;
}

li {
  list-style: none;
}

header {
  height: 200px;
  position: relative;
  overflow: hidden;
  z-index: 2;
}

header h1 {
  position: relative;
  z-index: 2;
}

header img {
  z-index: 0;
  position: absolute;
  top: 0;
  left: 0;
  height: auto;
  filter: sepia(1) opacity(60%);
}

nav {
  position: relative;
  font-family: 'Aladin', cursive;
  font-size: 2.5rem;
  margin: 30px 20px;
}

nav a:hover {
  text-decoration: none;
}

nav li {
  cursor: pointer;
  text-shadow: 2px 2px 10px rgba(0,0,0, .5);
  margin: 0 50px;
}

ul.skills {
  line-height: 2rem;
}

ul.skills li {
  list-style-type: circle;
}

footer {
  font-size: 48px;
  justify-content: center;
}

footer a {
  color: #fafafa;
  opacity: .5;
  margin: 10px 20px;
  transition: all .3s ease-in-out
}

footer a:hover {
  opacity: 1;
}

textarea {
  width: 600px;
  height: 200px;
  border: 3px solid #cccccc;
  padding: 5px;
  margin-top: 10px;
}

.container {
  width: 85%;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
}

.flex-row {
  display: flex;
  flex-wrap: wrap;
}

.space-between {
  justify-content: space-between;
}

.flex-end {
  justify-content: flex-end;
}

.flex-center {
  justify-content: center;
}

.mx-1 {
  margin-right: calc(1rem * .5);
  margin-left: calc(1rem * .5);
}

.mx-2 {
  margin-right: calc(1rem);
  margin-left: calc(1rem);
}

.my-1 {
  margin-top: calc(1rem * .5);
  margin-bottom: calc(1rem * .5);
}

.my-2 {
  margin-top: calc(1rem * .5);
  margin-bottom: calc(1rem * .5);
}

.my-5 {
  margin-top: calc(2.5rem);
  margin-bottom: calc(2.5rem);
}

.px-1 {
  padding-right: calc(1rem * .5);
  padding-left: calc(1rem * .5);
}

.px-2 {
  padding-right: calc(1rem);
  padding-left: calc(1rem);
}

.py-1 {
  padding-top: calc(1rem * .5);
  padding-bottom: calc(1rem * .5);
}

.py-2 {
  padding-top: calc(1rem);
  padding-bottom: calc(1rem);
}

.card {
  width: 25%;
  text-align: center;
}

.project {
  position: relative;
  width: 100%;
  max-width: 500px;
  height: 300px;
  margin: 30px;
}

.project-bg {
  position: relative;
  width: 100%;
  height: 100%;
}

.project-text {
  color: #31343D;
  position: absolute;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  padding: 50px;
  z-index: 900;
  opacity: 0;
  transition: all .5s ease-in-out;
}

.project-text:hover {
  background-color: rgba(255,255,255,.7);
  opacity: 1;
  border: 10px solid #31343D;
}

.project-text h3 {
  font-size: 2.5rem
}

.project-text a {
  text-decoration: none;
  font-weight: bold;
  color: #799FBA;
  transition: all .3s ease-in-out
}

.project-text a:hover {
  color: #1F3B60;
}

.navActive {
  color: #31343D;
  cursor: default;
  text-shadow: 3px 3px 15px rgba(255,255,255, .5);
}